var query, sortquery;
import {mapState,mapMutations} from 'vuex'
export default {
	name: "b2b-sortbox",
	props:{
		pstyle:"",
		pfixed:false,
	},
	computed:{
		...mapState({
		  scrollTop: state => state.scrollTop
		})
	},
	data: function() {
		return {
			sortTop:1000,
			orderLabel: "综合排序",
			sortNavBoxFix: "",
			zhpxBoxClass: "",
			orderTab: "zhpx",
			choiceBoxClass: "",
			orderby: "",
			orderList: "",
			isFirst: true,
			per_page: 0,
			choiceClass: [],
			priceList: [],
			priceItem: "",
			discList: [],
			discItem: ""
		}
	},
	created: function() {
		query = uni.createSelectorQuery().in(this);
		this.getFilter();
	},
	
	methods: {
		choiceFinish: function() {
			this.isFirst = true;
			this.per_page = 0;
			this.choiceBoxClass = "";
			this.getList();
		},
		choiceClear: function() {
			var that = this;
			var choiceClass = this.choiceClass;
			for (var i in choiceClass) {
				choiceClass[i].classname = "";
			}
			that.choiceClass = choiceClass;
			that.discItem = "";
			that.priceItem = "";


		},
		setDisc: function(it) {
			if (this.discItem == it) {
				this.discItem = "";
			} else {
				this.discItem = it;
			}
		},
		setPrice: function(it) {
			if (this.priceItem == it) {
				this.priceItem = "";
			} else {
				this.priceItem = it;
			}
		},
		checkChoice: function(index) {
			var that = this;
			var choiceClass = this.choiceClass;
			for(var i in choiceClass){
				if(choiceClass[i].id==index){
					if(choiceClass[i].classname==""){
						choiceClass[i].classname="choice-item-active"
					}else{
						choiceClass[i].classname=""
					}
				}
			}
			this.choiceClass =  choiceClass;
			 

		},
		setOrder: function(v, k) {
			this.zhpxBoxClass = "";

			this.orderLabel = v;
			this.orderTab = 'zhpx';
			this.orderby = k;
			this.per_page = 0;
			this.isFirst = true;
			this.getList();

		},
		setOrderTab: function(t) {
			this.orderTab = t;
			this.orderby = t;
			this.zhpxBoxClass = "";

			this.per_page = 0;
			this.isFirst = true;
			if (t == 'choice') {
				if (this.choiceBoxClass == "") {
					this.choiceBoxClass = "block";
					uni.pageScrollTo({
						scrollTop: this.sortTop
					})
				} else {
					this.choiceBoxClass = "";
				}


			} else {
				this.choiceBoxClass = "";
				this.getList();
			}


		},

		toggleOrder: function() {
			this.choiceBoxClass = "";
			if (this.zhpxBoxClass == '') {
				this.zhpxBoxClass = "block";
				uni.pageScrollTo({
					scrollTop: this.sortTop
				})
			} else {
				this.zhpxBoxClass = "";
			}
		},
		getFilter: function() {
			var that = this;
			that.app.get({
				url: that.app.apiHost + "/module.php?m=b2b_shoplist&a=Filter&ajax=1",
				success: function(res) {
					that.orderList = res.data.orderList;
					that.discList = res.data.discList;
				
					that.priceList = res.data.priceList;
					var choiceClass = Array();
					for (var i in res.data.filterList) {
					
						choiceClass.push({
							id:i,
							name:res.data.filterList[i],
							classname:""
						});
					}
					that.choiceClass=choiceClass;
					
					setTimeout(function() {




						query.select('#sortNavBox').boundingClientRect(res => {
							that.sortTop = res.top;
						}).exec();

					}, 300)
				}
			})
		},
		getList:function(){
			var that=this;
			var choiceStr="";
			for(var k in that.choiceClass){
				if(that.choiceClass[k].classname!=''){
					if(choiceStr!=""){
						choiceStr+=",";
					}
					choiceStr+=that.choiceClass[k].id;
				}
			}
			var redata={
				discount:that.discItem,
				price:that.priceItem,
				choice:choiceStr,
				orderby:that.orderby,
			}
			this.$emit("call-parent",redata);

		}

	}
}
